@use "../util" as *;

.page__title {
    color: white;
    text-align: start;
    font-size: 2rem;
    padding: 4em 0;
    text-shadow: 3px 5px 3px rgba(0, 0, 0, 0.2);
    .title__header {
        position: relative;
        display: inline-block;
        &::after {
            position: absolute;
            content: "";
            width: 100%;
            height: 5px;
            background: linear-gradient(
                to right,
                hsl(201, 100%, 50%),
                hsl(201, 100%, 40%),
                hsl(207, 100%, 30%),
                hsl(201, 100%, 40%),
                hsl(201, 100%, 50%)
            );
            bottom: -20px;
            left: 0px;
            border-radius: 10px;
        }
    }
    @include breakpoint(medium) {
        font-size: 2.5rem;
    }
}

.footer-pages {
    .footer-top {
        margin-top: 0em;
    }
}
