@use "../util" as *;

.about__title {
    background: url("../assets/images/title-back/about-title.webp"), rgba(50, 50, 50, 0.9);
    background-blend-mode: darken;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
}

.vsmsmt {
    background: url("../assets/images/title-back/title-back.webp");
    background-size: cover;
    padding: 4em 0;
    margin: 2em 0;
    .vsmsmt__container {
        .vsm {
            background: white;
            padding: 2em;
            margin-bottom: 1em;
            border-radius: 10px;
            .vsm__title {
                color: #212121;
                margin-bottom: 0.5em;
            }
        }
        @include breakpoint(medium) {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 2em;
            .vsm {
                margin-bottom: 0;
            }
        }
    }
}

.president {
    padding: 2em 0em;
    .president__container {
        .about__right {
            .about__name {
                text-transform: uppercase;
                margin-bottom: 1em;
                color: #555;
            }
            .about__tag {
                font-size: 1.5rem;
                color: var(--blue);
                font-weight: 600;
                margin-bottom: 1em;
            }
            .about__des {
                margin-bottom: 1em;
            }
            img {
                width: 50%;
            }
        }
        @include breakpoint(medium) {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 2em;
            align-items: start;
        }
    }
}
