:root {
  --font-body: "Poppins", sans-serif;
}

html,
body {
  font-family: "Poppins", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --blue: hsl(207, 100%, 37%);
  --light-blue: hsl(201, 100%, 47%);
  --blue-rgb: #0068bd;
  --light-blue-rgb: #009cf0;
  --red: hsl(14, 87%, 58%);
  --body-text: hsl(0, 0%, 20%);
  --title-text: hsl(219, 100%, 8%);
  --some-background: hsl(236, 65%, 95%);
  --gradient: linear-gradient(to right, var(--blue), var(--light-blue));
  --gradient-reverse: linear-gradient(to right, var(--light-blue), var(--blue));
}

*,
*::before,
*::after {
  box-sizing: border-box;
  transition: all 250ms;
}

body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

html, body {
  overflow-x: hidden;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.4;
  position: relative;
  color: #616161;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3 {
  margin: 0;
  line-height: 1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.section__title {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  margin: 0.3em auto;
  background: linear-gradient(130deg, #00a6ff, #0085cc, #005499, #0085cc, #00a6ff);
  background-size: 200% auto;
  color: var(--blue);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 5s linear infinite;
}
@keyframes shine {
  to {
    background-position: 200% center;
  }
}
@media (min-width: 40em) {
  .section__title {
    font-size: 2.5em;
  }
}

.section__description {
  font-size: 1rem;
  margin: 1em auto;
  text-align: center;
}

.container {
  width: 85%;
  max-width: 75rem;
  margin: auto;
}

section {
  position: relative;
}
section .navbar {
  position: sticky;
  top: 0%;
  display: grid;
  grid-template-areas: "ham logo ." "link link link";
  align-items: center;
  padding: 0.3em 1em;
  box-shadow: 0px 10px 10px 0px rgba(0, 0, 0, 0.3);
}
section .navbar .hamburger {
  grid-area: ham;
  width: 25px;
  height: 3px;
  background: var(--title-text);
  border-radius: 100px;
  transition: all 250ms;
}
section .navbar .hamburger::after, section .navbar .hamburger::before {
  display: block;
  content: "";
  background: var(--title-text);
  height: 3px;
  border-radius: 100px;
  width: 115%;
  transition: all 250ms;
}
section .navbar .hamburger::after {
  transform: translateY(-11px);
}
section .navbar .hamburger::before {
  transform: translateY(8px);
}
section .navbar .nav-logo {
  grid-area: logo;
}
section .navbar .nav-logo img {
  width: 40%;
  margin: auto;
}
section .navbar .nav-links {
  display: block;
  grid-area: link;
  list-style-type: none;
  text-align: center;
  padding-left: 0;
  max-height: 0;
  overflow: hidden;
  transition: all 250ms;
  margin-bottom: -0.5em;
}
section .navbar .nav-links .nav__link {
  color: var(--blue);
  font-weight: 600;
  padding: 1em 0;
  display: block;
  transition: all 250ms;
  position: relative;
}
section .navbar .nav-links .nav__link::after, section .navbar .nav-links .nav__link::before {
  position: absolute;
  content: "";
  width: 0%;
  height: 3px;
  background: var(--gradient);
  transition: width 300ms;
}
section .navbar .nav-links .nav__link::after {
  right: 10%;
  bottom: 0;
}
section .navbar .nav-links .nav__link::before {
  left: 10%;
  top: 0;
}
section .navbar .nav-links .nav__link:hover::after, section .navbar .nav-links .nav__link:hover::before {
  width: 80%;
  transition: width 300ms;
}
@media (min-width: 40em) {
  section .navbar {
    grid-template-areas: "logo link link link link link";
  }
  section .navbar .hamburger {
    display: none;
  }
  section .navbar .nav-logo {
    text-align: center;
  }
  section .navbar .nav-logo img {
    width: 50%;
    margin: auto;
  }
  section .navbar .nav-links {
    max-height: 1000px;
    padding: 0.5em 0;
    margin: 0.7em auto;
  }
  section .navbar .nav-links .nav__link {
    display: inline-block;
    padding: 0.5em 1em;
  }
}

